


























import Vue from "vue";

import VueQr from 'vue-qr'

export default Vue.extend({
  components: {
    VueQr,
  },
  props: {
    ssid: String,
    password: String,
    security: String,

    header: String,
    footer: String,
  },
  computed: {
    qr_code_value (): string {
      return(`WIFI:S:${this.ssid};T:${this.security};P:${this.password};;`)
    }
  },
})
